import React, { Component } from 'react';
import {
  IonApp,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonList,
  IonItem,
  IonButton,
  IonAlert,
  AlertInput,
  AlertButton
} from '@ionic/react';
import IDulces from './models/IDulces';
import '@ionic/react/css/core.css';
import DulceCard from './components/DulcesCard';

/**
 * Interfaz para definir el estado de App.
 */
interface AppState {
  listaDeDulces: IDulces[];
  mostrarMAñadir: boolean;
  mostrarMEliminarLista: boolean;
  mostrarTotal: boolean;
}

/**
 * Componente de React donde estara la interfaz principal de la 
 * aplicación.
 */
export default class App extends Component {
  state: AppState = {
    listaDeDulces: [],
    mostrarMAñadir: false,
    mostrarMEliminarLista: false,
    mostrarTotal: false
  };

  private entradaProducto: AlertInput = {
    name: 'producto',
    type: 'text',
    id: 'producto',
    placeholder: 'Producto'
  };

  private entradaCantidad: AlertInput = {
    name: 'cantidad',
    type: 'number',
    id: 'cantidad'
  };

  private entradaPrecio: AlertInput = {
    name: 'precio',
    type: 'number',
    id: 'precio'
  };

  private botonCancelar: AlertButton = {
    text: 'Cancel',
    role: 'cancel',
    cssClass: 'danger'
  };

  private añadirDulces = () => this.setState({ mostrarMAñadir: true });

  private eliminarDulces = () => this.setState({ mostrarMEliminarLista: true });

  private calcularTotal = () => this.setState({ mostrarTotal: true });

  private total = () => {
    let total: number = 0;
    this.state.listaDeDulces.map(i => total += (i.precio * i.cantidad));
    return total;
  };

  render() {
    return (
      <IonApp>
        <IonHeader>
          <IonToolbar>
            <IonTitle>Caja</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            {this.state.listaDeDulces.map(i => (
              <IonItem>
                <DulceCard producto={i.producto}
                           cantidad={i.cantidad}
                           precio={i.precio} />
              </IonItem>
            ))}
          </IonList>
          <IonButton shape="round" onClick={this.añadirDulces}>
            Añadir
          </IonButton>
          <IonButton shape="round" onClick={this.eliminarDulces}
                     color="danger">
            Eliminar Lista
          </IonButton>
          <IonButton shape="round" onClick={this.calcularTotal}>
            Calcular Total
          </IonButton>
        </IonContent>

        <IonAlert isOpen={this.state.mostrarMAñadir}
                  onDidDismiss={() => this.setState({mostrarMAñadir: false})}
                  header="Añadir"
                  inputs={[
                    this.entradaProducto,
                    this.entradaCantidad,
                    this.entradaPrecio
                  ]}
                  buttons={[
                    this.botonCancelar,
                    {
                      text: 'Ok',
                      handler: data => {
                        const { cantidad, precio, producto } = data;
                        if (cantidad && precio && producto && producto !== '') {
                          let nuevosDulces: IDulces = {
                            cantidad, precio, producto
                          };
                          this.setState((ps: AppState) => {
                            return {
                              listaDeDulces: [...ps.listaDeDulces, nuevosDulces]
                            }
                          });
                        }
                      }
                    }
                  ]} />

        <IonAlert isOpen={this.state.mostrarMEliminarLista}
                  onDidDismiss={() => this.setState(
                    {mostrarMEliminarLista: false})}
                  header="Eliminar lista de dulces"
                  buttons={[
                    this.botonCancelar,
                    {
                      text: 'Ok',
                      handler: () => this.setState({ listaDeDulces: [] })
                    }
                  ]} />

        <IonAlert isOpen={this.state.mostrarTotal}
                  onDidDismiss={() => this.setState({mostrarTotal: false})}
                  header="Total:"
                  message={"<h4>$" + this.total().toString() + "</h4>"}
                  buttons={[
                    {
                      text: 'OK'
                    }
                  ]} />
      </IonApp>
    );
  }
}